const PaymentRepository = require('../repositories/PaymentRepository');
const { validatePayment } = require('../validators/paymentValidator');
const { formatCurrency, formatDate } = require('../utils/formatters');
const { calculateOverdueDays, calculateInterest } = require('../utils/calculations');

class PaymentService {
  constructor() {
    this.paymentRepository = new PaymentRepository();
  }

  async getAllPayments(page = 1, limit = 10, filters = {}) {
    try {
      const offset = (page - 1) * limit;
      const payments = await this.paymentRepository.findAll(offset, limit, filters);
      const total = await this.paymentRepository.count(filters);

      return {
        payments: payments.map(this.formatPayment),
        pagination: {
          page: parseInt(page),
          limit: parseInt(limit),
          total,
          totalPages: Math.ceil(total / limit)
        }
      };
    } catch (error) {
      throw new Error(`Erro ao buscar pagamentos: ${error.message}`);
    }
  }

  async getPaymentById(id) {
    try {
      const payment = await this.paymentRepository.findById(id);
      if (!payment) {
        throw new Error('Pagamento não encontrado');
      }
      return this.formatPayment(payment);
    } catch (error) {
      throw new Error(`Erro ao buscar pagamento: ${error.message}`);
    }
  }

  async createPayment(paymentData) {
    try {
      // Validar dados do pagamento
      const validation = validatePayment(paymentData);
      if (!validation.isValid) {
        throw new Error(`Dados inválidos: ${validation.errors.join(', ')}`);
      }

      // Verificar se o contrato existe
      const contractExists = await this.paymentRepository.checkContractExists(paymentData.contract_id);
      if (!contractExists) {
        throw new Error('Contrato não encontrado');
      }

      // Processar dados do pagamento
      const processedData = this.processPaymentData(paymentData);
      
      const payment = await this.paymentRepository.create(processedData);
      return this.formatPayment(payment);
    } catch (error) {
      throw new Error(`Erro ao criar pagamento: ${error.message}`);
    }
  }

  async updatePayment(id, paymentData) {
    try {
      // Verificar se o pagamento existe
      const existingPayment = await this.paymentRepository.findById(id);
      if (!existingPayment) {
        throw new Error('Pagamento não encontrado');
      }

      // Não permitir alterar pagamentos já pagos
      if (existingPayment.status === 'pago') {
        throw new Error('Não é possível alterar pagamento já realizado');
      }

      // Validar dados do pagamento
      const validation = validatePayment(paymentData, true); // partial validation
      if (!validation.isValid) {
        throw new Error(`Dados inválidos: ${validation.errors.join(', ')}`);
      }

      // Processar dados do pagamento
      const processedData = this.processPaymentData(paymentData);
      
      const payment = await this.paymentRepository.update(id, processedData);
      return this.formatPayment(payment);
    } catch (error) {
      throw new Error(`Erro ao atualizar pagamento: ${error.message}`);
    }
  }

  async deletePayment(id) {
    try {
      // Verificar se o pagamento existe
      const payment = await this.paymentRepository.findById(id);
      if (!payment) {
        throw new Error('Pagamento não encontrado');
      }

      // Não permitir excluir pagamentos já pagos
      if (payment.status === 'pago') {
        throw new Error('Não é possível excluir pagamento já realizado');
      }

      await this.paymentRepository.delete(id);
      return { message: 'Pagamento excluído com sucesso' };
    } catch (error) {
      throw new Error(`Erro ao excluir pagamento: ${error.message}`);
    }
  }

  async updatePaymentStatus(id, status) {
    try {
      const validStatuses = ['pendente', 'pago', 'atrasado', 'cancelado'];
      if (!validStatuses.includes(status)) {
        throw new Error('Status inválido');
      }

      const payment = await this.paymentRepository.findById(id);
      if (!payment) {
        throw new Error('Pagamento não encontrado');
      }

      const updateData = { status };
      
      // Se está marcando como pago, definir data de pagamento
      if (status === 'pago' && !payment.payment_date) {
        updateData.payment_date = new Date().toISOString();
      }

      const updatedPayment = await this.paymentRepository.updateStatus(id, updateData);
      return this.formatPayment(updatedPayment);
    } catch (error) {
      throw new Error(`Erro ao atualizar status do pagamento: ${error.message}`);
    }
  }

  async confirmPayment(id, confirmationData) {
    try {
      const payment = await this.paymentRepository.findById(id);
      if (!payment) {
        throw new Error('Pagamento não encontrado');
      }

      if (payment.status === 'pago') {
        throw new Error('Pagamento já foi confirmado');
      }

      const updateData = {
        status: 'pago',
        payment_date: confirmationData.payment_date || new Date().toISOString(),
        payment_method: confirmationData.payment_method || payment.payment_method,
        notes: confirmationData.notes || payment.notes
      };

      const confirmedPayment = await this.paymentRepository.update(id, updateData);
      return this.formatPayment(confirmedPayment);
    } catch (error) {
      throw new Error(`Erro ao confirmar pagamento: ${error.message}`);
    }
  }

  async searchPayments(searchTerm, page = 1, limit = 10) {
    try {
      const offset = (page - 1) * limit;
      const payments = await this.paymentRepository.search(searchTerm, offset, limit);
      const total = await this.paymentRepository.countSearch(searchTerm);

      return {
        payments: payments.map(this.formatPayment),
        pagination: {
          page: parseInt(page),
          limit: parseInt(limit),
          total,
          totalPages: Math.ceil(total / limit)
        }
      };
    } catch (error) {
      throw new Error(`Erro ao buscar pagamentos: ${error.message}`);
    }
  }

  async getOverduePayments(page = 1, limit = 10) {
    try {
      const offset = (page - 1) * limit;
      const payments = await this.paymentRepository.findOverdue(offset, limit);
      const total = await this.paymentRepository.countOverdue();

      // Calcular dias de atraso para cada pagamento
      const paymentsWithOverdue = payments.map(payment => ({
        ...this.formatPayment(payment),
        overdue_days: calculateOverdueDays(payment.due_date),
        interest_amount: calculateInterest(payment.amount, payment.due_date)
      }));

      return {
        payments: paymentsWithOverdue,
        pagination: {
          page: parseInt(page),
          limit: parseInt(limit),
          total,
          totalPages: Math.ceil(total / limit)
        }
      };
    } catch (error) {
      throw new Error(`Erro ao buscar pagamentos em atraso: ${error.message}`);
    }
  }

  async getPaymentsDueToday(page = 1, limit = 10) {
    try {
      const offset = (page - 1) * limit;
      const payments = await this.paymentRepository.findDueToday(offset, limit);
      const total = await this.paymentRepository.countDueToday();

      return {
        payments: payments.map(this.formatPayment),
        pagination: {
          page: parseInt(page),
          limit: parseInt(limit),
          total,
          totalPages: Math.ceil(total / limit)
        }
      };
    } catch (error) {
      throw new Error(`Erro ao buscar pagamentos que vencem hoje: ${error.message}`);
    }
  }

  async getPaymentStats(period = '30d') {
    try {
      const stats = await this.paymentRepository.getStats(period);
      
      return {
        total_payments: stats.total_payments || 0,
        paid_payments: stats.paid_payments || 0,
        pending_payments: stats.pending_payments || 0,
        overdue_payments: stats.overdue_payments || 0,
        total_amount: formatCurrency(stats.total_amount || 0),
        paid_amount: formatCurrency(stats.paid_amount || 0),
        pending_amount: formatCurrency(stats.pending_amount || 0),
        overdue_amount: formatCurrency(stats.overdue_amount || 0),
        payment_rate: stats.total_payments > 0 
          ? Math.round((stats.paid_payments / stats.total_payments) * 100)
          : 0
      };
    } catch (error) {
      throw new Error(`Erro ao calcular estatísticas de pagamentos: ${error.message}`);
    }
  }

  async getRevenueChart(period = '12m', groupBy = 'month') {
    try {
      const chartData = await this.paymentRepository.getRevenueChart(period, groupBy);
      
      return chartData.map(item => ({
        ...item,
        amount: formatCurrency(item.amount || 0)
      }));
    } catch (error) {
      throw new Error(`Erro ao gerar gráfico de receita: ${error.message}`);
    }
  }

  async bulkUpdatePayments(paymentIds, updateData) {
    try {
      if (!Array.isArray(paymentIds) || paymentIds.length === 0) {
        throw new Error('Lista de IDs de pagamentos é obrigatória');
      }

      const processedData = this.processPaymentData(updateData);
      const updatedPayments = await this.paymentRepository.bulkUpdate(paymentIds, processedData);
      
      return {
        message: `${updatedPayments.length} pagamentos atualizados com sucesso`,
        updated_count: updatedPayments.length,
        payments: updatedPayments.map(this.formatPayment)
      };
    } catch (error) {
      throw new Error(`Erro ao atualizar pagamentos em lote: ${error.message}`);
    }
  }

  async exportPayments(filters = {}, format = 'csv') {
    try {
      const payments = await this.paymentRepository.findForExport(filters);
      
      if (format === 'csv') {
        return this.generateCSV(payments);
      } else {
        return JSON.stringify(payments.map(this.formatPayment), null, 2);
      }
    } catch (error) {
      throw new Error(`Erro ao exportar pagamentos: ${error.message}`);
    }
  }

  async getPaymentsByContract(contractId, page = 1, limit = 10) {
    try {
      const offset = (page - 1) * limit;
      const payments = await this.paymentRepository.findByContract(contractId, offset, limit);
      const total = await this.paymentRepository.countByContract(contractId);

      return {
        payments: payments.map(this.formatPayment),
        pagination: {
          page: parseInt(page),
          limit: parseInt(limit),
          total,
          totalPages: Math.ceil(total / limit)
        }
      };
    } catch (error) {
      throw new Error(`Erro ao buscar pagamentos do contrato: ${error.message}`);
    }
  }

  async getPaymentsByClient(clientId, page = 1, limit = 10) {
    try {
      const offset = (page - 1) * limit;
      const payments = await this.paymentRepository.findByClient(clientId, offset, limit);
      const total = await this.paymentRepository.countByClient(clientId);

      return {
        payments: payments.map(this.formatPayment),
        pagination: {
          page: parseInt(page),
          limit: parseInt(limit),
          total,
          totalPages: Math.ceil(total / limit)
        }
      };
    } catch (error) {
      throw new Error(`Erro ao buscar pagamentos do cliente: ${error.message}`);
    }
  }

  async generateReceipt(id) {
    try {
      const payment = await this.paymentRepository.findById(id);
      if (!payment) {
        throw new Error('Pagamento não encontrado');
      }

      if (payment.status !== 'pago') {
        throw new Error('Só é possível gerar recibo para pagamentos realizados');
      }

      return {
        receipt_number: `REC-${payment.id}`,
        payment_id: payment.id,
        contract_number: payment.contract?.contract_number,
        client_name: payment.contract?.client?.name,
        amount: formatCurrency(payment.amount),
        payment_date: formatDate(payment.payment_date),
        payment_method: payment.payment_method,
        generated_at: formatDate(new Date())
      };
    } catch (error) {
      throw new Error(`Erro ao gerar recibo: ${error.message}`);
    }
  }

  async sendPaymentReminder(id, method = 'email') {
    try {
      const payment = await this.paymentRepository.findById(id);
      if (!payment) {
        throw new Error('Pagamento não encontrado');
      }

      if (payment.status === 'pago') {
        throw new Error('Não é possível enviar lembrete para pagamento já realizado');
      }

      // Aqui seria implementada a lógica de envio de lembrete
      // Por enquanto, apenas simular o envio
      
      return {
        message: `Lembrete de pagamento enviado via ${method}`,
        payment_id: id,
        method,
        sent_at: new Date().toISOString()
      };
    } catch (error) {
      throw new Error(`Erro ao enviar lembrete de pagamento: ${error.message}`);
    }
  }

  // Métodos auxiliares
  processPaymentData(data) {
    const processed = { ...data };

    // Processar datas
    if (processed.due_date) {
      processed.due_date = new Date(processed.due_date).toISOString();
    }
    if (processed.payment_date) {
      processed.payment_date = new Date(processed.payment_date).toISOString();
    }

    // Processar valores numéricos
    if (processed.amount) {
      processed.amount = parseFloat(processed.amount);
    }

    // Definir status padrão
    if (!processed.status) {
      processed.status = 'pendente';
    }

    return processed;
  }

  formatPayment(payment) {
    if (!payment) return null;

    return {
      ...payment,
      amount: formatCurrency(payment.amount),
      due_date: formatDate(payment.due_date),
      payment_date: formatDate(payment.payment_date),
      created_at: formatDate(payment.created_at),
      updated_at: formatDate(payment.updated_at)
    };
  }

  generateCSV(payments) {
    const headers = [
      'ID',
      'Contrato',
      'Cliente',
      'Valor',
      'Data Vencimento',
      'Data Pagamento',
      'Status',
      'Método Pagamento',
      'Parcela'
    ];

    const rows = payments.map(payment => [
      payment.id,
      payment.contract?.contract_number || '',
      payment.contract?.client?.name || '',
      payment.amount,
      formatDate(payment.due_date),
      formatDate(payment.payment_date),
      payment.status,
      payment.payment_method || '',
      payment.installment_number || ''
    ]);

    return [headers, ...rows]
      .map(row => row.map(field => `"${field || ''}"`).join(','))
      .join('\n');
  }
}

module.exports = PaymentService;